@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&family=Roboto+Slab:wght@500&family=Source+Serif+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&family=Roboto+Slab:wght@200;300;500&family=Source+Serif+Pro&display=swap);
body {
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family:
    source-code-pro,
    Menlo,
    Monaco,
    Consolas,
    'Courier New',
    monospace;
}

.NavTitle {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  color: #0290ff !important;
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #888;
}

.mainForm {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.mainForm div {
  font-family: "Montserrat", sans-serif;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.mainForm div input {
  margin: auto 10px;
}

.mainForm div button {
  font-size: small;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  width: 500px;
  height: 45px;
  padding: 0.801rem 1.188rem 0.886rem 1.375rem;
}

.submitForm {
  height: 40px;
  width: 300px;
}

.bookEntry {
  display: flex;
  flex-direction: row;
  min-width: 80%;
  border: 1px solid rgba(0, 0, 0, 0.125) !important;
  margin-top: 20px;
  margin-bottom: 20px;
  justify-content: space-around;
}

.bookProgress {
  width: 80px;
  height: 80px;
  justify-self: end;
  justify-content: end;
  justify-items: end;
  align-self: center;
}

.progressInformation {
  display: flex;
  flex-direction: row;
  align-content: center;
  text-align: center;
  align-items: center;
}

.secondInformation {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: space-between;
  text-align: center;
  align-items: center;
}

.booksWrapper {
  align-items: center;
}

.progressDetails {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.progressDetails h6 {
  font-family: 'Roboto Slab', serif;
  font-size: 0.813rem;
  color: #0f0e0e80;
}

.progressDetails h5 {
  font-family: 'Roboto Slab', serif;
  font-size: 1rem;
  font-weight: 300;
}

.bookCategory {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  color: #0f0e0e80;
}

.bookTitle {
  font-family: 'Roboto Slab', serif;
  font-weight: 500;
  color: #121212;
}

.textualProgress {
  text-align: left;
}

.textualProgress h5 {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 2rem;
  color: #121212;
}

.textualProgress h6 {
  font-family: 'Montserrat', sans-serif;
  color: #0f0e0e80;
}

.bookAuthor {
  font-family: 'Roboto Slab', serif;
  font-weight: 200;
  color: #4386bf;
}

